import React from "react";
import {Switch, Route} from "react-router";
import Home from "./components/Home";
import Career from './components/Career';
import Events from "./components/Events";
import Classes from"./components/Classes";
import Contact from './components/Contact'
import About from "./components/About"
import ClassDetail from "./components/ClassDetail"
import Team from "./components/Team";
import CareerDetail from './components/CareerDetail';
import FAQ from './components/FrequentQuestions';
import Facilities from "./components/Facilities";
import testGal from "./components/testGal";


const Router = () => {
    return (
        <Switch>
        <Route exact path="https://benabaron.github.io/edukids-production/" component={Home} />
        <Route exact path="https://benabaron.github.io/edukids-production/About" component={About} />
        <Route exact path="https://benabaron.github.io/edukids-production/Career" component={Career} />
        <Route exact path="https://benabaron.github.io/edukids-production/Events" component={Events} />
        <Route exact path="https://benabaron.github.io/edukids-production/Classes" component={Classes} />
        <Route exact path="https://benabaron.github.io/edukids-production/Contact" component={Contact} />
        <Route exact path="https://benabaron.github.io/edukids-production/ClassDetail" component={ClassDetail} />
        <Route exact path="https://benabaron.github.io/edukids-production/Team" component={Team} />
        <Route exact path="https://benabaron.github.io/edukids-production/CareerDetails" component={CareerDetail} />
        <Route exact path="https://benabaron.github.io/edukids-production/FAQ" component={FAQ} />
        <Route exact path="https://benabaron.github.io/edukids-production/Facilities" component={Facilities} />
        <Route exact path="https://benabaron.github.io/edukids-production/testGal" component={testGal} />
        </Switch>
    )
}

export default Router; 
